import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import ExplainerAnimationDescription from "../../../components/explainer-animations-page-components/explainer-animations-page-description/ExplainerAnimationDescription";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"

import { useLocation } from "react-router-dom"

export default function ExplainerAnimations() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="explainer-animations-main-container">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="Explainer Animations"
        bannerParagraph="Simplify complex ideas and captivate your audience with our expertly crafted explainer animations."
        bannerButton1="LEARN MORE"
        bannerButton2="OUR WORK"
      />

      <ExplainerAnimationDescription headingOne="As a video marketing agency, we are experts at producing enthralling explainer animations that boost sales for your company. Our experts mix visual storytelling, captivating animations, and smart messaging to effectively express your brand's value proposition and demystify difficult ideas. With the use of our explainer animations, we can increase audience engagement, conversion rates, and the effectiveness of your digital marketing campaigns." />

      <SubCategoriesCardLeft
        heading="Build Brand Recognition: Tell Your Story"
        paragrapgh="It helps in sharing your brand's narrative and establishes a strong bond with your audience. Our animations are made specifically for your brand, using your colors, logo, and distinctive visual aesthetic. Integrating the animation with your overall branding strategy produces a unified and memorable experience that improves brand awareness and leaves a lasting impression."
        button="LET'S CHAT"
        picture={pic1}
      />

      <SubCategoriesCardRight
        heading="Adaptability and Versatility: Reach Your Target Audience"
        paragraph="Our explainer animations are designed to adapt to various platforms and devices, ensuring maximum reach and accessibility. Whether it's your website, social media channels, presentations, or trade shows, our animations seamlessly integrate into your marketing efforts. With our versatile animations, you can communicate your message effectively."
        button="LET'S CHAT"
        picture={pic2}
      />

      <br />
      <br />
      <br />
      <br />

      <Pricing />

      <br />
      <br />
      <br />

      <Skills />

      <br />
      <br />
      <br />

      <Ourwork />

      <Footer />
    </div>
  );
}
